body {
  margin: 0 auto;
  max-width: 35em;
  font-family: "Helvetica", "Arial", sans-serif;
  line-height: 1.5;
  padding: 1em 1em;
  color: #444;
  font-size: 12pt;

}

h1 {
  text-align: center;
  margin-bottom: 0.2em;
  padding-bottom: 0;
  font-size: 200%
}

h4 {
  text-align: center;
}

h4, h5 {
  font-weight: bold;
}

p {
  margin-bottom: 0.5em;
}

img {
  margin-bottom: 0.5em;
  margin-left: auto;
  margin-right: auto;
  display: block
}

hr {
  margin: 0.5em;
}

.navbar .navbar-nav {
  display: inline-block;
  float: right;
  vertical-align: middle;
}

.navbar .navbar-collapse {
  text-align: center;
}

.navbar-brand {
  font-size: 150%;
}

.fa, .fas {
  width: 14pt;
  font-size: 14pt;
  color: #24477f;
}

.fa:hover, .fas:hover {
  color: #C00000;
}

.bg-navyblue {
  background-color: #24477f;
  border-color: #24477f
}

.bg-maroon {
  background-color: #C00000;
  border-color: #C00000 
}

.bg-gray {
  background-color: #F0F0F0;
  border-color: #F0F0F0;
}




.btn-default {
  margin-left: 0.5em;
  background-color: #24477f;
}

.btn-default:hover {
  background-color: #C00000;
}